.section-history {
  box-sizing: border-box;
  padding: 10px;
  margin-top: 10%;
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  background-color: #8b8b8b;
  opacity: 95%;
}
.section-history .content-history {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
  background-color: #8b8b8b;
  opacity: 95%;
  margin: 10;
  padding: 0.5em;
  border: 10px solid #ccc;
  width: auto;
  word-wrap: break-word;
}
.section-history .body-history {
  outline: none;
  resize: none;
  margin-top: 0;
}
.section-history .history-delete-all {
  width: 100px;
  height: 30px;
  background-color: #ccc;
  margin: 10px;
  justify-content: center;
  align-items: center;
  transform: translateX(50%);
}
.section-history .history-delete-all:hover {
  background-color: #aaa;
}
.section-history .history-delete-all:active {
  background-color: #999;
}
.section-history #json-pretty {
  background: rgb(236, 232, 232);
  line-height: 1.3;
  white-space: pre-wrap; /* Since CSS 2.1 /
white-space: -moz-pre-wrap;  / Mozilla, since 1999 /
white-space: -pre-wrap;      / Opera 4-6 */
  white-space: -o-pre-wrap;
  word-wrap: break-word;
}
.section-history __json-key__ {
  color: rgb(70, 8, 145);
}
.section-history .__json-value__ {
  color: rgb(253, 176, 130);
}
.section-history .__json-string__ {
  color: rgb(77, 159, 14);
}
.section-history .__json-boolean__ {
  color: rgb(156, 53, 115);
}
.section-history .__json-pretty-error__ {
  line-height: 1.3px;
  color: rgb(248, 248, 242);
  background: #1e1e1e;
  overflow: auto;
}/*# sourceMappingURL=history.css.map */